html, body{ 
    /*background-color: #f1f2f6;*/
    /*background-color: #d8cdfd;*/
    background-image: linear-gradient(319deg, #cdedfd 0%, #ffec82 37%, #ffcfd2 100%);
    min-height: 100%;
}
.container{
    /*background-color: #d8cdfd;
    background-image: linear-gradient(319deg, #cdedfd 0%, #ffec82 37%, #ffcfd2 100%);*/
    background-color: white;
    border-radius: 12px;
    width: 70%;
    height: 100%;
    align-content: center;
}
/* Smartphones (landscape) ----------- */
@media (min-width : 321px) {
  .container{
    background-color: white;
    border-radius: 12px;
    width: 70%;
}
}

/* Smartphones (portrait) ----------- */
@media (max-width : 320px) {
  .container{
    background-color: white;
    border-radius: 12px;
    width: 93%;
}
}
.row-header{
    margin:0px auto;
    padding: 0px auto;

}
.row-content{
    margin:0px auto;
    padding:50px 0px 50px 0px;
    border-bottom: 1px ridge;
    min-height: 400px;
}
.footer{
    background-color: #D1C4E9;
    margin:0px auto;
    padding: 20px 0px 20px 0px;
}
.jumbotron{
    padding: 70px 30px 70px 30px;
    margin: 0px auto;
    background: #ffffff;
}
.address{
    font-size: 80%;
    margin: 0px;
    color: #0f0f0f;
}
.nav-link {
    align-content: right;
    color: rgba(0, 0, 0, 0.479);
}
.nav-link:hover {
    color: black;
}
.nav-link:visited {
    color: rgba(0, 0, 0, 0.479);
}
.h4 {
    align-items: center;
}
.row{
    margin-top: 2.4%;
}
p {
    font-size: 100%;
    color: #252525bb;
    text-align: justify;
}
#home {
    color: black;
    font-weight: 600;
}
#quote {
    background: linear-gradient(319deg, #663dff 0%, #aa00ff 37%, #cc4499 100%);
    -webkit-background-clip: text;
    background-clip: text;
	-webkit-text-fill-color: transparent;
    font-weight: 600;
}
.contacts{
    word-spacing: 5px;
}
.fa {
    color:rgba(0, 0, 0, 0.479);
    font-weight: 400;
}
.fa-github:hover {
    color: #333;
}
.fa-linkedin:hover {
    color: #0976b4;
}
.fa-envelope:hover {
    color: #00bcf2;
    /*color: #252525e7;*/
}
.proj-title{
    color: black;
    text-decoration: none;
}
.card{
    border-radius: 4px;
    border: none;
}
.card:hover{
    opacity: 0.85;
    transition: opacity 0.5s;
}
i{
  padding: 4px;
}
a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}

